.login-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center horizontally */
  justify-content: center; /* Center vertically */
  height: 330px; /* Adjust as needed */
  background-color: var(--primary-color);
  width: 540px;
  position: relative;
  border-radius: 30px;
}

.login_main_container {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}

.login-loader-container {
  display: 'grid';
  place-items: 'center';
  height: '100vh';
}

.login_img_container {
  position: absolute;
  top: 55px;
  width: 270px;
}
