@import './assets/theme/variables.scss';

body {
  margin: 0;
  // font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  //   'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  //   sans-serif;
  font-family: var(--app-regular-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  --heading1: 14px;
  --heading2: 16px;
  --heading3: 18px;
  --heading4: 20px;
  --heading5: 22px;
}

/* global fonts */
@font-face {
  font-family: "JosefinSlabRegular";
  src: url('./assets/fonts/JosefinSlab-Regular.ttf');
  font-weight: 400;
}

@font-face {
  font-family: "JosefinSlabSemiBold";
  src: url('./assets/fonts/JosefinSlab-SemiBold.ttf');
  font-weight: 700;
}

@font-face {
  font-family: "JosefinSlabMedium";
  src: url('./assets/fonts/JosefinSans-Medium.ttf');
  font-weight: 600;
}


@font-face {
  font-family: "JosefinSansRegular";
  src: url('./assets/fonts/JosefinSans-Regular.ttf');
  font-weight: 400;
}

@font-face {
  font-family: "JosefinSansMedium";
  src: url('./assets/fonts/JosefinSans-Medium.ttf');
  font-weight: 600;
}

@font-face {
  font-family: "JosefinSansSemiBold";
  src: url('./assets/fonts/JosefinSans-SemiBold.ttf');
  font-weight: 700;
}