@import '../../../../App.scss';

.sideNav-container {
    display: flex;
    flex-direction: column;
    width: 60px;
    background: var(--primary-color);
    border-radius: 20px;
    padding: 20px 10px;
    height: calc(100% - 40px);
    transition: width 0.5s ease;
    position: relative;
    z-index: 99;
}

.sub-container {
    height: 100%;
}

.app-banner {
    display: grid;
    justify-content: center;
    padding-bottom: 15px;
    border-bottom: solid 1px var(--secondary-color);
}

.logout {
    padding: 5px 10px;
    border-top: solid 1px var(--secondary-color);
    cursor: pointer;
    color: var(--secondary-color);
}

.logout:hover {
    background-color: var(--secondary-color);
    color: var(--tertiary-color);
    border-radius: 20px;

    img {
        background-color: var(--tertiary-color) !important;
    }
}

.logout:hover img {
    background-color: var(--secondary-color);
    color: var(--tertiary-color);
}

.logout>img {
    margin: 0 5px;
}

.menu-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    padding: 5px 10px;
    gap: 10px;
}

.menu-container>div {
    cursor: pointer;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 10px 0;
    gap: 5px;
}

.menu-container>div>img {
    width: 20px;
    height: 20px;
    margin: 0 10px;
}

.menu-container:hover {
    color: var(--tertiary-color);
}

.menu-container>div:hover {
    background-color: var(--secondary-color);
    border-radius: 20px;
    color: var(--tertiary-color);

    img {
        background-color: var(--tertiary-color) !important;
        color: var(--tertiary-color);
    }
}

.settings {
    display: flex;
    justify-content: center;
    padding: 5px 10px;
    cursor: pointer;

    // padding: 5px 10px;
    // height: 34px;
    // width: 20px;
    /* border-radius: 50px; */

}

.settings>img {
    margin: 0px 5px;
}

.settings:hover {
    background-color: var(--secondary-color);
    color: var(--tertiary-color);
    border-radius: 20px;

    img {
        background-color: var(--tertiary-color) !important;
    }
}

.settings:hover img {
    background-color: var(--secondary-color);
    color: var(--tertiary-color);
}

.outer-container {
    margin: 10px 0;
    padding: 0;

    // display: flex;
    // justify-content: center;

}

.handle>#img {
    // width: 5px;
    // height: 12px;
    margin-left: -10px;
    color: var(--secondary-color);
}


.handle {
    background-color: var(--primary-color);
    width: 15px;
    height: 60px;
    position: absolute;
    display: grid;
    place-items: center;
    top: calc(50% - 40px);
    left: 80px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    cursor: pointer;
    border: none;
    visibility: hidden;
    margin-left: -15px;
    transition: margin-left 0.5s ease, left 0.5s ease, visibility 0.5s ease;
}

.sideNav {
    position: relative;
}

.extend {
    width: 300px;
    box-shadow: 40px 20px 45px 0px #00000040;
}

.opened {
    left: 320px !important;
}

.opened>#img {
    transform: rotate(180deg);
    margin-left: -12px;
}

.menu-text {
    margin: 0;
}

.menu-text:hover {
    color: var(--tertiary-color);
}

.banner-image {
    height: 30px;
}

.expanded {
    justify-content: flex-start !important;
    gap: 20px !important;
    padding: 10px !important;
}

.gap-unset {
    gap: unset !important
}

.pl-5 {
    padding-left: 10px !important;
//    margin: 0px 5px;
}

.pl-10 {
    // margin-left:5px;
    // margin: 0px 10px;
}

.sub-container:hover>.handle {
    visibility: visible;
    margin-left: 0;
}

.post-info {
    display: flex;
    gap: 15px;
    margin: 15px 0;
}

.menu-selected {
    background-color: var(--secondary-color);
    color: var(--tertiary-color);
    border-radius: 20px;

    img {
        background-color: var(--tertiary-color) !important;
    }
}

.menu-deselected {
    background-color: var(--primary-color);
    color: var(--secondary-color);
}

.menu-selected:hover {
    color: var(--tertiary-color);
}

.selected-text {
    color: var(--tertiary-color);
} 

.tip {
    z-index: 999;
    // background-color: var(--tertiary-color) !important;
}

.tooltip {
    border-radius: 15px !important;
    background-color: var(--tertiary-color) !important;
    width: 180px !important;
    height: 40px !important;
    text-align: center;
    display: grid;
    place-items: center;
    @extend .medium-18;
    visibility: visible !important;
}

.MuiTooltip-arrow {
    color: var(--tertiary-color) !important;
}

.no_visible {
    visibility: hidden !important;
}