.sideNav {
    grid-area: menu;
}

.header {
    grid-area: header;
}

.main-content {
    grid-area: main;
    height: 100% !important;
    overflow: hidden;
    padding-right: 20px;
}

.parent {
    display: grid;
    grid-template-columns: 80px auto;
    grid-template-areas: 
                        'menu header'
                        'menu main';
    grid-template-rows: 80px auto;
    height: calc(100vh - 40px);
    overflow: hidden;
    padding: 20px 0 20px 20px;
    background-color: var(--quaternary-color);
}

.parent > .header,
.parent > .main-content {
    grid-column-start: 1;
    margin-left: 100px;
}

.opened-navBar {
    grid-template-columns: 300px auto !important;
}

.loader-container {
    z-index: 9999;
}
