.close_icon {
    &_view {
        margin: 0;
        padding: 32px;
        background: transparent !important;
        position: relative;
        box-shadow: none !important;
    }
}

.cancel_icon_holder {
    height: 40px;
    width: 40px;
    background: var(--primary-color);
    display: grid;
    place-items: center;
    border-radius: 50%;
    position: absolute;
    top: 12px;
    right: 15px;
    cursor: pointer;
}

.icon_close {
    color: var(--secondary-color);
}

.dialouge-title-container {
    background-color: var(--primary-color);
    color: var(--secondary-color);
    text-align: center;
    font-family: var(--app-regular-font) !important;
    border-radius: 10px 10px 0 0;
}

.popup-close-container {
    position: absolute;
    top: 50px;
    right: 35px;
    color: white;
    cursor: pointer;
    align-content: end;
}
