.preview-model-container {
    background-color: transparent;
}

.preview-close-container {
    position: absolute;
    top: 12px;
    right: 10px;
    color: white;
    cursor: pointer;
}

.preview-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
}

.action-container {
    width: 50wh;
    height: 50wh;
}

.preview-icon_holder {
    height: 32px;
    width: 32px;
    background-color: var(--secondary-color);
    border-radius: 50%;
    display: grid;
    place-items: center;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    cursor: pointer;
    z-index: 99999;
}

.preview-left-arrow {
    @extend .preview-icon_holder;
    left: 12px;
    transform: rotate(180deg);
}

.preview-right-arrow {
    @extend .preview-icon_holder;
    right: 12px;
}